<template>
  <div class="login">
    <form action="#">
        <ul class="form-list border-list">
            <li>
                <label></label>
                <input v-model="mobileNum" name="mobile" maxlength="11" type="tel" placeholder="">
            </li>
           <!--  <li>
                <label></label>
                <input type="password" placeholder="">
            </li> -->
            <li>
                <label></label>
                <input type="tel" placeholder="" name="mobileCode" maxlength="6" v-model="mobileCode">
                <button @click.prevent="getVerifyCode" class="btn-getCode" :class="{disbtn:!rightMobile}" v-show="!computedTime"></button>
                <button @click.prevent="getVerifyCode" class="btn-getCode disbtn" v-show="computedTime">({{computedTime}}s)</button>
            </li>
        </ul>
      <!--   <div class="clearfix pdr">
            <a class="font-gray fr"></a>
        </div> -->
        <div class="btn">
            <a class="btn-theme" @click="mobileLogin"></a>
        </div>
        
    </form>

    <alert-tip v-show="showAlertTip" :alertText="alertText"></alert-tip>
   
  </div> 
</template>

<script>
import alertTip from '../../components/alertTip'
import {mapMutations} from 'vuex'
import {mobileCode, checkExsis, sendLogin} from '../../service/getData'
export default {
  name: 'login',
  data () {
    return {
        mobileNum: null,//
        computedTime: 0, //
        userInfo: null, //
        validate_token: null, //
        mobileCode: null, //
        alertText: "",
        showAlertTip: false,
        arr:[]
    }
  },
  components: { 
    alertTip
  },
  computed: {
    //
    rightMobile() {
        return  /^(13[0-9]|15[0-9]|18[0-9])\d{8}$/.test(this.mobileNum)
     },
     //
    rightCode() {
        return /^\d{6}$/gi.test(this.mobileCode)
    }
  },
  methods: {
    ...mapMutations([
        'RECORD_USERINFO',
    ]),
    //
    async getVerifyCode(){
        if(this.rightMobile) {//
            this.computedTime = 30;
            this.timer = setInterval(() => {
                this.computedTime --;
                if (this.computedTime == 0) {
                    clearInterval(this.timer)
                }
            }, 1000);
             //
            let exsis = await checkExsis(this.mobileNum,'mobile');
            if (exsis.message) {
                this.showHideAlert(exsis.message);
                return
            }else if(!exsis.is_exists) {
                this.showHideAlert("");
                return
            }
            //
            let res = await mobileCode(this.mobileNum,'mobile');
            if (res.message) {
                alert('hehe')
                return
            }
            this.validate_token = res.validate_token;
        }
    },
    //
    async mobileLogin(){
        if (!this.rightMobile) {
           this.showHideAlert('');
            return
        }else if(!this.rightCode){
            this.showHideAlert('');
            return
        }
        //
        this.arr = await sendLogin(this.mobileCode,this.mobileNum,this.validate_token);   
        this.userInfo = this.arr[0];
          // console.log(sendLogin())                                 
        //
        if (!this.userInfo.user_id) {
            alert(this.userInfo.message);
        }else{
            this.RECORD_USERINFO(this.userInfo);
            // console.log(this.userInfo.user_id)
            this.$router.go(-1);
            
        }
    },
    //
    showHideAlert(text) { 
        this.showAlertTip = true;
        this.alertText = text;
        setTimeout(() => {
            this.showAlertTip = false;
        }, 1000);
    }

  }
  
}
</script>



